import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StoreModule } from '@ngrx/store';
import * as fromHome from './store/reducers/home.reducer';
import { EffectsModule } from '@ngrx/effects';
import { HomeEffects } from './store/effects/home.effects';
import { BannerComponent } from './components/banner/banner.component';
import { HomeComponent } from './container/home/home.component';
import { CategoryItemsComponent } from './components/category-items/category-items.component';
import { LatestJobsComponent } from './components/latest-jobs/latest-jobs.component';
import { WorkshopTraningComponent } from './components/workshop-traning/workshop-traning.component';
import { TotalRecordsComponent } from './components/total-records/total-records.component';
import { DownloadsComponent } from './components/downloads/downloads.component';

@NgModule({
  declarations: [BannerComponent, HomeComponent, CategoryItemsComponent, LatestJobsComponent, WorkshopTraningComponent, TotalRecordsComponent, DownloadsComponent],
  imports: [
    CommonModule,
    StoreModule.forFeature('home', fromHome.reducer),
    EffectsModule.forFeature([HomeEffects])
  ],
  exports: [BannerComponent, HomeComponent],
})
export class HomeModule { }
