import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';

import { HeaderComponent } from './components/header/header.component';
import { FooterComponent } from './components/footer/footer.component';
import { ContentComponent } from './components/content/content.component';
import * as fromLayout from './store/reducers/layout.reducer';
import { LayoutEffects } from './store/effects/layout.effects';
import { AppRoutingModule } from '@app/app-routing.module';
import * as featureModules from '@app/modules';

@NgModule({
  declarations: [HeaderComponent, FooterComponent, ContentComponent],
  imports: [
    CommonModule,
    AppRoutingModule,
    featureModules.HomeModule,
    EffectsModule.forFeature([LayoutEffects]),
    StoreModule.forFeature('layout', fromLayout.reducer)
  ],
  exports: [HeaderComponent, FooterComponent, ContentComponent],
})
export class LayoutModule { }
