import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StoreModule } from '@ngrx/store';
import * as fromSignin from './store/reducers/signin.reducer';
import { EffectsModule } from '@ngrx/effects';
import { SigninEffects } from './store/effects/signin.effects';
import { SigninComponent } from './container/signin/signin.component';

@NgModule({
  declarations: [SigninComponent],
  imports: [
    CommonModule,
    StoreModule.forFeature('signin', fromSignin.reducer),
    EffectsModule.forFeature([SigninEffects])
  ]
})
export class SigninModule { }
