import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './modules/home/container/home/home.component';
import { SigninComponent } from './modules/signin/container/signin/signin.component';
import { RegisterComponent } from './modules/register/container/register/register.component';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent, data: { title: 'Home' } },
  { path: 'signin', component: SigninComponent, data: { title: 'Signin' } },
  { path: 'register', component: RegisterComponent, data: { title: 'register' } }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
